@import "https://fonts.googleapis.com/css2?family=Podkova:wght@400..800&family=Ubuntu+Condensed&display=swap";
@import "https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap";
.container {
  box-sizing: border-box;
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  padding: 0 8px;
}

@media (width >= 320px) {
  .container {
    max-width: 320px;
    padding: 0 10px;
  }
}

@media (width >= 768px) {
  .container {
    max-width: 768px;
    padding: 0 12px;
  }
}

@media (width >= 1200px) {
  .container {
    max-width: 1200px;
    padding: 0 15px;
  }
}

.footer__text {
  color: #fff;
}

.header {
  background-color: var(--color-graphite);
  font-family: var(--podkova-font);
  box-sizing: border-box;
  background: none;
}

@media screen and (width >= 1200px) {
  .header {
    padding-top: 30px;
  }
}

.flex {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__logo {
  border-radius: 50%;
  width: 50px;
  height: 50px;
}

@media screen and (width >= 1200px) {
  .header__logo {
    width: 80px;
    height: 80px;
  }
}

.header__burger {
  cursor: pointer;
  background: none;
  border: 0;
  margin-right: 8px;
  padding: 8px;
  display: none;
}

.header__burger-icon {
  width: 28px;
  height: 28px;
  color: var(--text-color);
  transition: transform .25s, opacity .2s;
}

.header__burger.open .header__burger-icon {
  transform: rotate(90deg)scale(1.05);
}

.header__nav {
  padding-left: 12px;
  padding-right: 12px;
}

.header__panel {
  background: none;
  border-radius: 8px;
  align-items: center;
  gap: 12px;
  padding: 8px 12px;
  display: flex;
  position: relative;
}

.header__link {
  color: var(--text-color);
  font-size: 1rem;
  font-weight: 500;
  text-decoration: none;
  transition: color .2s, background .2s;
  display: inline-block;
}

@media (width <= 768px) {
  .header__burger {
    display: inline-flex;
  }

  .header__nav {
    z-index: 1200;
    justify-content: flex-start;
    align-items: flex-start;
    display: none;
    position: fixed;
    inset: 0;
  }

  .header__nav.open {
    display: flex;
  }

  .header__nav .header__panel {
    width: 100%;
    height: 100%;
    color: var(--text-color);
    padding: calc(env(safe-area-inset-top, 20px)  + 36px) 18px 18px;
    box-shadow: none;
    backdrop-filter: none;
    z-index: 1250;
    box-sizing: border-box;
    background: #0c0c0cf5;
    border-radius: 0;
    flex-direction: column;
    gap: 18px;
    display: flex;
    position: relative;
    overflow-y: auto;
  }

  .header__nav .header__list {
    flex-direction: column;
    align-items: stretch;
    gap: 24px;
    margin: 0;
    padding: 0 12px;
    display: flex;
    width: 100% !important;
    max-width: none !important;
  }

  .header__nav .header__list .header__link {
    color: var(--text-color);
    text-align: center;
    padding: 12px 10px;
    font-size: 1.05rem;
  }

  .header__nav .header__list .header__item {
    width: 100%;
  }

  .header {
    position: relative;
  }
}

.mobile-modal {
  display: none;
}

.mobile-modal.open {
  display: block;
}

.mobile-modal__backdrop {
  z-index: 1200;
  background: #0000008f;
  position: fixed;
  inset: 0;
}

.mobile-modal__content {
  width: 92%;
  max-width: 440px;
  color: var(--text-color);
  z-index: 1250;
  background: #0c0c0ce0;
  border-radius: 12px;
  flex-direction: column;
  gap: 12px;
  padding: 20px 18px 26px;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 18px 50px #0009;
}

.mobile-modal__close {
  color: var(--text-color);
  cursor: pointer;
  background: none;
  border: 0;
  font-size: 24px;
  line-height: 1;
  position: absolute;
  top: 10px;
  right: 10px;
}

.mobile-modal__nav .header__list {
  flex-direction: column;
  align-items: center;
  gap: 12px;
  margin: 16px 0 0;
  display: flex;
}

.mobile-modal__nav .header__link {
  text-align: center;
  width: 100%;
  padding: 8px 6px;
  font-size: 1.05rem;
  display: block;
}

.mobile-modal__nav .header__list {
  align-items: stretch;
  width: 100%;
  max-width: 420px;
  margin: 16px auto 0;
}

.mobile-modal__nav .header__list .header__item {
  width: 100%;
}

.mobile-modal__nav .header__list .header__link {
  width: 100%;
  padding: 12px 14px;
  display: block;
}

@media (width >= 769px) {
  .mobile-modal {
    display: none !important;
  }

  .header__panel-close, .mobile-modal__close {
    visibility: hidden;
    display: none !important;
  }
}

.header__panel-close {
  color: var(--text-color);
  cursor: pointer;
  background: none;
  border: 0;
  justify-content: center;
  align-items: center;
  padding: 6px;
  display: inline-flex;
  position: absolute;
  top: 12px;
  right: 12px;
}

.header__panel {
  position: relative;
}

.header__panel-close svg {
  opacity: .95;
}

.header__panel-close:hover {
  background: #ffffff0a;
  border-radius: 8px;
}

.header__panel-close:focus {
  border-radius: 8px;
  outline: 2px solid #ffffff1f;
}

.panel--hidden {
  opacity: 0;
  display: none !important;
  transform: none !important;
}

.header__overlay-close {
  z-index: 1260;
  color: var(--text-color);
  cursor: pointer;
  background: #ffffff0a;
  border: 0;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  display: inline-flex;
  position: fixed;
  top: 18px;
  right: 18px;
  box-shadow: 0 6px 18px #00000073;
}

.header__overlay-close:hover {
  background: #ffffff0f;
}

.header__overlay-close svg {
  opacity: .95;
}

.burger__bar {
  background: var(--text-color);
  width: 22px;
  height: 2px;
  margin: 4px 0;
  transition: transform .25s, opacity .2s;
  display: block;
}

@media (width <= 768px) {
  .header__list {
    flex-direction: column;
    gap: 52px;
    padding: 16px;
    display: none;
  }

  .header__burger {
    display: inline-flex;
  }

  .header__nav.open .header__list {
    z-index: 1000;
    background: #0000000a;
    border-radius: 8px;
    min-width: 160px;
    display: flex;
    top: 100%;
    right: 16px;
    box-shadow: 0 8px 20px #0000001f;
  }

  .header {
    position: relative;
  }
}

.header__link {
  color: var(--text-color);
  text-align: left;
  width: 250px;
  padding-bottom: 4px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
  position: relative;
}

@media screen and (width >= 1200px) {
  .header__link {
    text-align: center;
    width: 100%;
  }
}

.header__link:hover {
  color: var(--text-color) !important;
}

.header__link:after {
  content: "";
  transform-origin: 100%;
  background-color: #ff0;
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media screen and (width >= 1200px) {
  .header__link:after {
    content: "";
    transform-origin: 100%;
    background-color: #ff0;
    width: 100%;
    height: 2px;
    transition: transform .3s;
    position: absolute;
    bottom: 0;
    left: 0;
    transform: scaleX(0);
  }
}

.header__link:hover:after {
  transform-origin: 0;
  transform: scaleX(1);
}

.work__list {
  color: #fff;
  text-align: left;
}

.hero {
  color: #fff;
  background-image: url("background.60ce8b9d.webp");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: var(--background-color);
  height: 100vh;
  font-family: var(--podkova-font);
  justify-content: center;
  align-items: center;
  margin-top: -200px;
  padding: 100px 0;
  display: flex;
}

.hero__description {
  max-width: 600px;
}

.hero__logo {
  border-radius: 20px;
}

.hero__text {
  background-color: var(--background-color);
  border-radius: 10px;
  outline: 2px solid #ff0;
  margin-left: auto;
  padding: 10px 20px;
}

.gena {
  color: #000;
  background-color: #ff0;
  height: 200px;
  margin-top: 0;
  margin-bottom: 0;
}

.gena__title {
  text-align: center;
  padding-top: 50px;
  font-family: Great Vibes, cursive;
  font-size: 54px;
  font-style: normal;
  font-weight: 400;
}

.galery {
  background-color: #f5f5f5;
  padding: 60px 0;
}

.galery__title {
  text-align: center;
  color: #333;
  margin-bottom: 40px;
  font-size: 36px;
}

.galery__container {
  background-color: #fff;
  border-radius: 8px;
  max-width: 800px;
  margin: 0 auto;
  position: relative;
  box-shadow: 0 2px 10px #0000001a;
}

.galery__images {
  width: 100%;
  height: 500px;
  position: relative;
  overflow: hidden;
}

.galery__image {
  object-fit: contain;
  opacity: 0;
  z-index: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: opacity .4s;
  position: absolute;
  top: 0;
  left: 0;
}

.galery__image.active, .galery__image:first-child {
  opacity: 1;
  z-index: 1;
}

.galery .buttons {
  z-index: 2;
  pointer-events: none;
  justify-content: space-between;
  padding: 0 20px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
}

.galery .button__left, .galery .button__right {
  color: #fff;
  cursor: pointer;
  pointer-events: auto;
  background: #00000080;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  font-size: 20px;
  transition: background-color .3s;
  display: flex;
}

.galery .button__left:hover, .galery .button__right:hover {
  background-color: #000c;
}

.galery .button__left:disabled, .galery .button__right:disabled {
  cursor: not-allowed;
  background-color: #0003;
}

li {
  list-style: none;
}

body {
  color: #000;
  background-color: var(--background-color);
}

.trainer-card__photo {
  border: 2px solid #ff0;
  border-radius: 20px;
  width: 150px;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (width >= 1200px) {
  .trainer-card__photo {
    width: 350px;
  }
}

.trainer-card {
  flex-direction: column;
  display: flex;
}

@media screen and (width >= 1200px) {
  .trainer-card {
    flex-direction: row;
  }
}

.trainer__flex {
  flex-direction: column;
  display: flex;
}

@media screen and (width >= 1200px) {
  .trainer__flex {
    margin-left: 20px;
  }
}

.trainers-section__track {
  flex-direction: column;
  gap: 100px;
  padding-left: 0;
  display: flex;
}

.trainer-card__name {
  color: #ff0;
  text-align: center;
  padding-top: 30px;
}

@media screen and (width >= 1200px) {
  .trainer-card__name {
    text-align: left;
  }
}

.trainer-card__text {
  text-align: justify;
  color: #fff;
  max-width: 600px;
  margin-top: 20px;
}

.trainers-section__title {
  text-align: center;
  color: #fff;
  margin-top: 50px;
  margin-bottom: 50px;
}

html {
  scrollbar-gutter: stable;
}

header {
  border-bottom: var(--color-accent) solid 1px;
  box-sizing: border-box;
}

body {
  background-color: var(--background-color);
  overflow-y: scroll;
}

.history__image {
  opacity: 0;
  border: 2px solid #ff0;
  border-radius: 20px;
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  animation: 1.2s forwards fadeInZoom;
  display: block;
  transform: scale(.9);
}

@media screen and (width >= 1200px) {
  .history__image {
    width: 500px;
  }
}

@keyframes fadeInZoom {
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.history__description {
  opacity: 0;
  transition: all .8s;
  transform: translateY(30px);
}

.history__description.visible {
  opacity: 1;
  transform: translateY(0);
}

.history__description, .history__vovk-before, .history__description-vovk, .history__vovk-after {
  opacity: 0;
  transition: opacity .8s, transform .8s;
  transform: translateY(30px);
}

.history__vovk-after {
  border: 2px solid #ff0;
  border-radius: 20px;
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

@media screen and (width >= 1200px) {
  .history__vovk-after {
    width: 500px;
  }
}

.history__vovk-before {
  border: 2px solid #ff0;
  border-radius: 20px;
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

@media screen and (width >= 1200px) {
  .history__vovk-before {
    width: 500px;
  }
}

.history__vovk-before {
  transform: translateX(-40px);
}

.history__vovk-after {
  transform: translateX(40px);
}

.visible {
  opacity: 1;
  transform: translate(0);
}

.debug {
  outline: 1px dashed #00000014;
  padding: 10px;
}

.history__title {
  text-align: center;
  margin-bottom: 100px;
}

.article {
  background-color: var(--color-white);
  box-shadow: 0 4px 20px var(--color-shadow);
  border-radius: 16px;
  padding: 40px;
}

.article__title {
  color: var(--color-graphite-dark);
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem;
  font-weight: 700;
}

.to-top {
  z-index: 1000;
  backdrop-filter: blur(6px);
  opacity: 0;
  pointer-events: none;
  background: linear-gradient(#ffffff0f, #ffffff05);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 64px;
  height: 64px;
  transition: transform .35s cubic-bezier(.2, .9, .3, 1), opacity .35s, box-shadow .2s;
  display: inline-flex;
  position: fixed;
  bottom: 30px;
  right: 30px;
  transform: translateY(40px)scale(.95);
  box-shadow: 0 8px 24px #00000059;
}

.to-top img {
  width: 100%;
  height: 100%;
  display: block;
}

.to-top.show {
  opacity: 1;
  pointer-events: auto;
  transform: translateY(0)scale(1);
}

.to-top:hover {
  transform: translateY(-4px)scale(1.03);
  box-shadow: 0 12px 30px #00000073;
}

.to-top img {
  object-fit: contain;
  width: 48px;
  height: 48px;
}

.article__description {
  text-align: center;
  color: var(--color-text-secondary);
  margin-bottom: 40px;
  font-size: 1.1rem;
}

.to-top.in-section {
  position: fixed;
  bottom: 16px;
  right: 16px;
}

.article__list-title, .recomandation__title, .prohibitions__title, .duty__title, .membership__title {
  color: var(--color-graphite-dark);
  border-left: 4px solid var(--color-accent);
  margin: 30px 0 15px;
  padding-left: 10px;
  font-size: 1.4rem;
  font-weight: 700;
}

.membership__description {
  color: var(--color-text-secondary);
  margin-bottom: 10px;
  font-style: italic;
}

.article__list, .recomandation__list, .prohibitions__list, .duty__list, .membership__list {
  margin-bottom: 40px;
  padding-left: 20px;
  list-style: inside decimal;
}

.article__item, .recomandation__item, .prohibitions__item, .duty__item, .membership__item {
  background-color: var(--color-bg-light);
  border-left: 4px solid var(--color-border);
  color: var(--background-color);
  border-radius: 8px;
  margin-bottom: 10px;
  padding: 15px 20px;
  transition: all .3s;
}

.article__item:hover, .recomandation__item:hover, .prohibitions__item:hover, .duty__item:hover, .membership__item:hover {
  background-color: var(--color-bg-hover);
  border-left-color: var(--color-accent);
}

.artcle__item-text, .recomandation__text, .prohibitions__text, .duty__text, .membership__text {
  color: var(--color-graphite);
  font-size: 1rem;
  font-weight: 400;
}

.info__text, .info__text2, .info__text3 {
  color: var(--color-graphite-dark);
  background-color: var(--color-bg-light);
  border-left: 4px solid var(--color-accent);
  border-radius: 8px;
  margin-top: 20px;
  padding: 15px 20px;
  font-size: 1rem;
}

@media (width <= 768px) {
  .article {
    padding: 25px;
  }

  .article__title {
    font-size: 1.6rem;
  }

  .article__description {
    font-size: 1rem;
  }
}

@media (width <= 480px) {
  .container {
    padding: 20px 10px;
  }

  .article {
    padding: 20px;
  }

  .article__title {
    font-size: 1.4rem;
  }

  .to-top {
    width: 48px;
    height: 48px;
    bottom: 20px;
    right: 16px;
    transform: translateY(20px)scale(.95);
  }

  .to-top.show {
    transform: translateY(0)scale(1);
  }

  .to-top img {
    width: 36px;
    height: 36px;
  }
}

.to-top-container {
  position: relative;
}

.to-top.in-section {
  position: absolute;
  bottom: 16px;
  right: 16px;
  transform: translateY(0)scale(1);
  box-shadow: 0 8px 20px #00000047;
}

:root {
  --background-color: #383838;
  --text-color: #fff;
  --color-white: #fff;
  --color-graphite-dark: #2f2f2f;
  --color-graphite: #383838;
  --color-text-secondary: #555;
  --color-bg-light: #f9f9f9;
  --color-bg-hover: #f1f1f1;
  --color-border: silver;
  --color-shadow: #0000000d;
  --color-accent: yellow;
  --container-max-width: 1200px;
  --base-font: "Ubuntu Condensed", sans-serif;
  --podkova-font: "Podkova", serif;
}

.header {
  background-color: var(--background-color);
  border-bottom: var(--color-accent) solid 1px;
}

body {
  background-color: var(--background-color);
}

.work-schedule {
  background: #fff;
  border-radius: 14px;
  max-width: 300px;
  padding: 18px 20px;
  font-family: Inter, sans-serif;
  box-shadow: 0 4px 14px #0000000d;
}

.contacts {
  margin-top: 30px;
}

.work-schedule h3 {
  color: #111;
  border-bottom: 1px solid #eee;
  margin-top: 0;
  padding-bottom: 8px;
  font-size: 18px;
}

.schedule-list {
  margin: 12px 0;
}

.schedule-list div {
  color: #333;
  padding: 4px 0;
  font-size: 15px;
}

.today-status {
  margin-top: 10px;
  font-size: 15px;
  font-weight: 600;
}

#today-status {
  font-weight: 700;
}

.open {
  color: #1aaf5d;
}

.closed {
  color: #e63946;
}

span {
  color: #fff;
}

.link {
  color: #000;
  text-align: center;
  background-color: #ff0;
  border-radius: 8px;
  width: max-content;
  margin-top: 30px;
  margin-left: auto;
  padding: 10px;
  display: block;
}

.ramka {
  border: 2px solid #ff0;
  border-radius: 10px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 10px;
  padding-left: 10px;
  display: block;
}

@media screen and (width >= 1200px) {
  .ramka {
    width: 500px;
    padding-bottom: 30px;
    padding-left: 30px;
  }
}

.contacts__flex {
  flex-direction: column;
  align-items: flex-start;
  gap: 50px;
  display: flex;
}

@media screen and (width >= 1200px) {
  .contacts__flex {
    flex-direction: row;
  }
}

.socials__number-phone {
  color: #fff;
}

.social-list {
  gap: 12px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.social-list li {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  overflow: hidden;
}

.social-list li a {
  background-color: #0000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  transition: background-color .3s;
  display: flex;
}

.social-list li a:hover {
  background-color: #ff0;
}

.social-list li img {
  width: 20px;
  height: 20px;
  display: block;
}

.socials {
  background-color: gray;
  border-radius: 30px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  margin-top: 50px;
  padding: 20px;
  display: flex;
