@import "../variebles";

html {
  scrollbar-gutter: stable; /* —Å—É—á–∞—Å–Ω–µ, –µ–ª–µ–≥–∞–Ω—Ç–Ω–µ —Ä—ñ—à–µ–Ω–Ω—è */
}

header {
    border-bottom: var(--color-accent) solid 1px;
     box-sizing: border-box;
}

body {
    background-color: var(--background-color);
     overflow-y: scroll;
}

.history__image {
  opacity: 0;
  transform: scale(0.9);
  animation: fadeInZoom 1.2s ease forwards;
  border: yellow solid 2px;
  border-radius: 20px;
  margin-left: auto;
  margin-right: auto;
    display: block;
    width: 300px;

     @media screen and (min-width: 1200px) {
        width: 500px;

        }
}

@keyframes fadeInZoom {
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.history__description {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.8s ease;
}

.history__description.visible {
  opacity: 1;
  transform: translateY(0);
}

 .history__description,
  .history__vovk-before,
  .history__description-vovk,
  .history__vovk-after {
    opacity: 0;
    transform: translateY(30px);
    transition: opacity 0.8s ease, transform 0.8s ease;
  }

  .history__vovk-after {
    border: yellow solid 2px;
    border-radius: 20px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    width: 300px;

     @media screen and (min-width: 1200px) {
        width: 500px;

        }
  }

  .history__vovk-before {
    border: yellow solid 2px;
    border-radius: 20px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    width: 300px;

    @media screen and (min-width: 1200px) {
        width: 500px;

        }
  }

  /* –†—ñ–∑–Ω—ñ –Ω–∞–ø—Ä—è–º–∫–∏ ‚Äî –æ–ø—Ü—ñ–π–Ω–æ */
  .history__vovk-before { transform: translateX(-40px); }
  .history__vovk-after  { transform: translateX(40px); }

  /* –ö–ª–∞—Å, —è–∫–∏–π –¥–æ–¥–∞—î–º–æ –∫–æ–ª–∏ –µ–ª–µ–º–µ–Ω—Ç —É –ø–æ–ª—ñ –∑–æ—Ä—É */
  .visible {
    opacity: 1;
    transform: translate(0,0);
  }

  /* –î—ñ–∞–≥–Ω–æ—Å—Ç–∏–∫–∞: —Ç–∏–º—á–∞—Å–æ–≤—ñ –±–æ—Ä–¥–µ—Ä–∏ —â–æ–± –±–∞—á–∏—Ç–∏ –º—ñ—Å—Ü—è –µ–ª–µ–º–µ–Ω—Ç—ñ–≤ */
  .debug {
    outline: 1px dashed rgba(0,0,0,0.08);
    padding: 10px;
  }

  .history__title {
    text-align: center;
    margin-bottom: 100px;
  }